.tutorial{
    h2{
        font-size: 32px;
        line-height: 42px;
        margin-bottom: 45px;
         @media #{$sm-layout}{
            margin-bottom: 30px;
        }
    }
    h3{
        font-size: 24px;
        line-height: 31px;
    }
    h4{
        font-size: 16px;
        line-height: 21px;
        color: #333333;
    }
    span{
        font-size: 12px;
        line-height: 16px;
        display: block;
        margin: 10px 0;
        @media #{$sm-layout}{
            margin-bottom: 20px;
        }
    }
}
.tutorial-content{
    padding: 75px 0;
    .btn-fill{
        width: 200px;
        font-weight: 400;
        &:hover{
            color: var(--meta_color);
        }
        @media #{$large-mobile}{
            width: 100%;
        }
    }
    .box.d-flex{
        align-items: center;
        gap: 40px;
        @media #{$sm-layout}{
            display: block !important;
        }
    }
    .box-text{
        p{
            margin: 0;
        }
        h3{
            margin-bottom: 10px;
        }
    }
    .m-65{
        margin: 65px 0;
    }
    .stars i{
        font-size: 13px;
        color: var(--button_color);
    }
}
.slick-slider{
    .box{
      
        padding: 0 30px;
        @media #{$sm-layout}{
            margin-left: 15px;
              padding: 0 0 0 15px;
        }
    }
    .box-text{
        margin: 20px 0 25px;
    }
}
.tutorial-section2{
    margin: 105px 0;
}
.slick-slider{
    margin: 0 -30px;
}
.slick-prev, .slick-next{
    width: 40px !important;
    height: 40px !important;
    top: 45% !important;
}
.slick-prev::before, .slick-next::before{
    font-family: "Font Awesome 5 Pro" !important;
    font-size: 40px !important;
    color: #000 !important;
}
.slick-prev::before{
    content: "\f104" !important;
}
.slick-next::before{
    content: "\f105" !important;
}
.box-text{
    flex: 1;
    a{
        color: #000;
        transition: .3s;
        &:hover{
            color: var(--primary_color);
        }
    }
}
.tutorial-thumb{
    overflow: hidden;
     @media #{$sm-layout}{
        margin-bottom: 20px;
     }
    img{
         transform: scale(1);
        transition: .4s ease;
        width: 100%;
    }
    img:hover{
        transform: scale(1.2);
         
    }
}