.section-heading{
     text-align: center;
    .entry-sub-title {
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 56px;
         text-align: center;
        .entry-icon {
            color: var(--primary_color);
            font-size: 18px;
        }
    }
    .entry-title {
        max-width: 500px;
        width: 100%;
        margin: 0 auto;
    }
    .entry-text {
        max-width: 430px;
        width: 100%;
    }
}
 

.slick-dot-nav {
    .slick-dots {
        bottom: 0;
        text-align: left;
        margin: 0 -10px;
        li {
            margin: 0 10px;
            height: 11px;
            width: 11px;
            button {
                border: none;
                background-color: #DBDEFF ;
                border-radius: 50%;
                height: 11px;
                width: 11px;
                transition: var(--transition);
                &::before {
                    display: none;
                }
            }
            &.slick-active {
                button {
                    background-color: var(--color-primary);
                }
            }
        }
    }
}
 
.slick-arrow-nav {
    .slick-arrow {
        height: 40px;
        width: 40px;
        background-color: rgba(0,0,0,0.2);
        border-radius: 50%;
        z-index: 1;
        transition: var(--transition);
        top: -52px;
        &::before {
            font-family: var(--font-awesome);
            opacity: 1;
            font-size: 16px;
        }
        &:hover {
            background-color: rgba(0,0,0,1);
        }
        &.slick-next { 
            right: 16px;
            &::before {
                content: "\f105";
            }
        }
        &.slick-prev {
            right: 73px;
            left: inherit;
            &::before {
                content: "\f104";
            }
        }
    }
}