.about-wrap{
    background-color: #FFE180;

    .container-fluid, .row, .row > * {
        padding: 0;
        margin: 0;
        align-items: center;
    }
    .p-l{
        padding-left: calc((100% - 1180px) / 2);
        @media #{$smlg-device} {
            padding-left: 20px;
        }
    }
    .left{
        padding: 115px 0;
        @media #{$md-layout} {
            padding: 60px 0 66px;
        }
        // @media #{$smlg-device} {
        //     padding: 60px 0 55px;
        // }

        h1{
            font-size: 32px;
            line-height: 42px;
        }
        p{
            width: 80%;
            line-height: 24px;
            margin-bottom: 40px;
        }
    }
    .right, .right img{
        width: 100%;
        height: 100%;
    }
}