.service-wrap{
    padding: 75px 0 45px;
    text-align: center;

    h1{
        font-size: 40px;
        letter-spacing: -0.05em;
        margin-bottom: 50px;
        padding: 0 40px;
    }
    h3{
        font-size: 24px;
        line-height: 31px;
        margin: 45px 0 10px;
    }
    a{
         transition: .7s;
        color: var(--body_color);
        &:hover{
            color: var(--primary_color);
        }
    }
    // p{
    //     width: 250px;
    //     margin: 0 auto 30px;
    // }
}
@media #{$sm-layout}{
    .service-wrap{
        padding: 60px 0 0;
        
        h1, p{
            margin-bottom: 80px;
        }
        .icon img{
            transform: scale(1.5);
        }
    }
}