.case-studies-wrap{
    padding: 65px 0 75px;
    @media #{$md-layout} {
        padding: 54px 0 60px;
    }

    .container{
        padding: 0;
    }
    .list-section{
        margin-top: 40px;
        @media #{$sm-layout} {
            padding-left: 40px;
        }
    }
    ul{
        margin: 0;
        padding: 0;
    }
    h3{
        font-size: 18px;
        line-height: 23px;
        margin-bottom: 45px;
    }
    p{
        margin-bottom: 30px;
    }
    li a{
        font-weight: 700;
        line-height: 28px;
        &:hover{
            transition: .3s !important;
            color: var(--button_color);
        }
        i{
            font-size: 14px;
            margin-left: 10px;
        }
    }
}