.tutorial-video{
    padding: 83px 0 105px;
    h2{
        font-size: 40px;
        line-height: 52px;
        margin: 0;
    }
    h4{
        font-size: 16px;
        line-height: 21px;
        color: #333333;
    }
    h5{
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
        margin: 0;
    }
    span{
        font-size: 12px;
        line-height: 16px;
    }
    ul{
        padding: 0;
    }
}
.tv-title{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 35px;
    a{
        color: #000;
    }
    i{
        font-size: 30px;
    }
}
.tv-thumbnail{
    position: relative;
    width: fit-content;
    margin-bottom: 20px;
    i{
        font-size: 120px;
        color: #000;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
.video-list{ 

    .accordion-item {
        border: none;
        
    }
    .accordion-button {
        color: #000;
        background-color: #F9F9F9;
        box-shadow: none;
    }
    margin: 30px 0 105px;
    .flex{
        display: flex;
        align-items: center;
        gap: 25px;
        margin-bottom: 10px;
    }
    ul{
        margin: 25px 20px;
    }
}
.bg-grey{
    background-color: #F9F9F9;
    padding: 0;
}
.tv-video, .tv-sec-top{
    width: fit-content;
}
.tv-video span{
    padding-left: 20px;
}
.li-heading-text{
    display: flex;
    align-items: center;
    h4, i{
        font-size: 24px;
        line-height: 31px;
    }
    h4{
        margin: 0;
    }
}
.li-heading-text i, .tv-length{
    margin: 0 65px 0 auto;
}
.tv-length{
    font-size: 14px;
    line-height: 18px;
}
.tv-related h2{
    margin-bottom: 45px;
}