.footer-wrap.style-1 {
    padding: 40px 110px;
    background-color: var(--meta_color);
    @media #{$large-mobile} {
        padding: 40px 30px;
    }
}

.footer-menu {
    flex-wrap: wrap;
    gap: 30px;
    li {

    @media #{$lg-layout} {
       width: 26%;
    }
    @media #{$md-layout} {
       width: 26%;
    }
    @media #{$sm-layout} {
       width: 46%;
    }
        a {
            color: var(--light-grey);
            font-size: 20px;
            line-height: 26px;
            &:hover{
                color: var(--primary_color);
            }
            &:after {    
                content: "";
                display: block;
                height: 2px;
                background: var(--primary_color);
                transition: width 0.3s ease 0s, left 0.3s ease 0s;
                width: 0;
            }
            &:hover:after { 
                width: 100%; 
            }
        }
    }
}

.footer-top{
    margin-bottom: 35px;
    @media #{$large-mobile}{
        margin-bottom: 55px;
    }
    ul{
        margin: 0;
        padding: 0;
    }
}
.footer-bottom{
    text-align: center;
    @media #{$large-mobile} {
        text-align: left;
    }
}

.footer-social-wrap{
    a{
         transition: width 0.3s ease 0s, left 0.3s ease 0s;
        &:hover{
            opacity: .5;
        }
    }
}