.documentation .container-fluid{
    padding: 0;
}
.documentaion-content{
    h1{
        font-size: 40px;
        line-height: 1.35;
        letter-spacing: -0.05em;
        margin-bottom: 20px;
        @media #{$md-layout}{
            font-weight: 500;
            font-size: 32px;
            line-height: 42px;
            margin-bottom: 20px;
        }
    }
    h3{
        font-size: 24px;
        line-height: 31px;
    }
    h5, p, li, span{
        font-size: 14px;
        line-height: 25px;
    }
    ul{
        padding: 0;
    }
}

.doc-left-sidebar{
    margin-top: 95px;
    margin-left: 40px;
    @media #{$laptop-device}{
        margin-top: 100px;
         margin-left: 26px;
    }

    @media #{$lg-layout}{
        margin-top: 100px;
         margin-left: 15px;
    }

}
.doc-right-sidebar{
    margin-top: 169px;
    margin-left: 35px;
    @media #{$md-layout}{
        display: none;
    }
}
.doc-inner-section{
    padding: 60px 30px 100px 60px;
    ul{
        list-style: disc;
        padding-left: 30px;
    }
}
.inner-section-top p{
    font-size: 18px;
    line-height: 23px;
    margin-bottom: 35px;

    @media #{$md-layout}{
        font-size: 14px;
        line-height: 18px;
    }
}
.inner-section-bottom{
    background: #F8F8F8;
    border-radius: 25px;
    padding: 35px 45px;
}
.smc-data{
    margin-bottom: 35px;
}
.g-10{
    gap: 10px;
}
.smc-content{
    padding-right: 146px;
    li{
        margin-bottom: 5px;
    }
}
#app-overview{
    display: none;
}
#edit-page{
    color: var(--title_color);
    font-weight: 400 !important;
    margin-top: 30px;
    transition: .3s;
    i{
        font-size: 22px;
    }
    span{
        font-size: 18px;
        line-height: 23px;
    }
    &:hover{
        color: var(--primary_color);
    }
}
.options{
    h4{
        font-size: 18px;
        line-height: 25px;
    }
    a{
        color: var(--title_color);
        font-weight: 400 !important;
    }
    li{
        margin-bottom: 20px;
        padding-left: 10px;
        line-height: 14px;
        transition: .1s;
        &:hover{
            border-left: 2px solid #959595;
        }
    }
}

.doc-main-menu > li > a{
    font-size: 28px !important;
    line-height: 36px !important;
    font-weight: 700;
    margin-bottom: 35px;
}
.doc-sub-menu > li > a{
    font-weight: 500;
    margin-bottom: 15px;
}
.doc-sub-sub-menu > li > a{     
    margin-bottom: 15px;
    margin-left: 15px;
    font-weight: 400;

     @media #{$laptop-device}{
         margin-left: 10px;
    }
}
.doc-menu > li > a{
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 18px;
    line-height: 25px;
    color: var(--title_color);
    position: relative;
       @media #{$laptop-device}{
        font-size: 16px;
         line-height: 24px;
    }
    &:hover{
        color: #2871FF;
    }
    i{
        font-size: 24px;

         @media #{$sm-layout} { 
            position: absolute;
            right: 20px; 

        }
    }
}
.doc-sub-menu{
    margin-bottom: 35px;
}

@media #{$sm-layout}{
    .doc-left-sidebar{
        margin-top: 35px;
        margin-left: 15px;
    }
    .doc-sub-sub-menu{
        display: none;
    }
    .doc-inner-section{
        padding: 20px 0 65px;
    }
    .inner-section-bottom{
        padding: 35px 15px;
    }
}


/*======================================*/
/*           Documentation A            */ 
/*======================================*/
.documentation-a{
    a{
        font-weight: 700;
    }
    .bottom-section2, .bottom-section4{
        padding: 35px 0;
        li{
            margin-bottom: 20px;
            line-height: 18px;
        }
    }
    .bottom-section3{
        ul{
            padding-top: 15px;
            padding-bottom: 45px;
        }
        li{
            margin-bottom: 10px;
            line-height: 18px;
            i{
                margin-left: 10px;
            }
        }
        p{
            line-height: 18px;
        }
    }
}


/*======================================*/
/*           Documentation B            */ 
/*======================================*/
.documentation-b{
    h2{
        font-size: 28px;
        line-height: 36px;
    }
    h4{
        font-size: 18px;
        line-height: 23px;
    }
    .bottom-section2, .bottom-section4{
        padding: 45px 0;
    }
    .tutorial-thumbnail{
        width: fit-content;
        position: relative;
        i{
            position: absolute;
            font-size: 64px;
            color: #ffffff;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
        }
    }
    @media #{$laptop-device}{
        .inner-section-bottom{
            padding-right: 150px;
        }
    }
}