.banner {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    
}
.banner-content{
    color: #fff;
    .entry-title{
        color: #fff;
        font-weight: 700;
        font-size: 72px;
        line-height: 75px;
        color: var(--primary_color);
        margin-bottom: 15px;
    }
    .entry-subtitle{
        font-size: 24px;
        line-height: auto;
        line-height: 31px;
        margin-bottom: 45px;
    }
    
    @media #{$sm-layout}{
        text-align: center;
        justify-content: center;
        .entry-content{
            padding: 0 20px;
        }
        .entry-title{
            font-size: 56px;
            line-height: 56px;
        }
        .entry-subtitle{
            font-size: 18px;
            line-height: 23px;
            margin: 40px 0;
        }
        br{
            display: none;
        }
    }
}

/*============ Banner 1 ============*/
.banner-1{
    background-image: url("../image/banner/banner1.jpg");
    .banner-content{
        min-height: 712px;
        display: flex;
        align-items: center;
        @media #{$sm-layout}{
            min-height: 632px;
        }
    }
}

/*============ Banner 2 ============*/
.banner-2{
    background-image: url("../image/banner/banner2.jpg");
    .banner-content{
        min-height: 440px;
        padding-top: 166px;
    }
}
 