.cta-section.cta-style-1{
    background-image: url("../image/cta/cta1.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    padding: 58px 0;
    @media #{$sm-layout} {
        padding: 60px 0 80px;
    }

    .box-section{
        @media #{$large-mobile} {
            padding-left:0px;
            .entry-text{

                padding-right: 15px;


            }
        }
    }
    h1{
        font-size: 48px;
        margin: 0;
    }
    p{
        font-size: 18px;
        margin: 10px 0 35px;
        line-height: 24px;
        @media #{$sm-layout} {
            margin: 20px 0 110px;
        }
    }
}