@mixin btn ($bg, $color){
    width: 180px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    border-radius: 5px;
    background-color: $bg;
    color: $color;
    border: 1px solid $bg;
    transition: all 0.4s ease;
    &:hover{
        background-color: transparent;
        color: $bg;
    }
}
.btn-fill { 
    @include btn(var(--button_color), var(--meta_color));
}

.btn-ghost {
    @include btn(var(--meta_color), #ffffff);

    @media #{$sm-layout} {
        width: 100%;
        margin: auto;
    }
}