html {
    height: 100%;
}

body {
    height: 100%;
    vertical-align: baseline;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
}

a {
    color: var(--secondary_color);
}

a,
a:active,
a:hover,
a:focus {
    text-decoration: none;
    outline: 0 none;
}

input,
textarea {
    width: 100%;
    padding: 10px 12px;
    outline: 0;
}

button {
    cursor: pointer;
}

svg {
    display: block;
}

img {
    max-width: 100%;
    height: auto;
}

ul {
    list-style: outside none none;
}

.wrapper {
    overflow: hidden;
}

body.loaded .wrapper {
    opacity: 1;
    visibility: visible;
}
 

@media (min-width: 1140px) {
    .container {
        max-width: 1180px;
    }
}

.smc-section {
    padding-top: 70px;
    padding-bottom: 75px;
    position: relative;
    @media (max-width: 1199px) {
        padding: 70px 0 75px;
    }
    @media (max-width: 991px) {
        padding: 70px 0 75px;
    }
    @media (max-width: 767px) {
        padding: 70px 0 75px;
    }
    @media (max-width: 575px) {
        padding: 70px 0 75px;
    }
} 

.smc-section-top {
    padding-top: 70px;
    position: relative;
    @media (max-width: 1199px) {
        padding: 70px 0 0;
    }
    @media (max-width: 991px) {
        padding: 70px 0 0;
    }
    @media (max-width: 767px) {
        padding: 70px 0 0;
    }
    @media (max-width: 575px) {
        padding: 70px 0 0;
    }
}

.smc-section-bottom {
    padding-bottom: 75px;
    position: relative;
    @media (max-width: 1199px) {
        padding: 0 0 75px;
    }
    @media (max-width: 991px) {
        padding: 0 0 75px;
    }
    @media (max-width: 767px) {
        padding: 0 0 75px;
    }
    @media (max-width: 575px) {
        padding: 0 0 75px;
    }
}  
 
