.header1{
    position: absolute;
    width: 100%;

    ul{
        margin: 0;
        padding: 0;
    }

    .navbar-content{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 30px 0;
    }
    .sidemenu-btn{
        display: flex;
        flex-direction: column;
        gap: 10px;
        cursor: pointer;
        span{
            display: block;
            width: 32px;
            border: 1px solid #fff;
            transition: .3s;
        }
        &:hover{
            span{
                border: 1px solid var(--primary_color);
            }
            span:first-of-type, span:last-of-type{
                width: 25px;
            }
        }
    }
    .template-main-menu{
        position: fixed;
        top: 0;
        right: -100%;
        background: #1C2633;
        height: 100vh;
        width: 350px;
        padding: 85px 40px;
        transition: .7s;
        overflow: auto;
        z-index: 22;
        @media #{$sm-layout}{
            height: 632px;
            width: 100%;
        }
    }
    .mainmenu{
        display: flex;
        flex-direction: column;
        gap: 40px;
        margin-bottom: 55px;
        @media #{$sm-layout}{
            gap: 35px;
            margin-bottom: 40px;
        }

        .nav-item a{
            font-size: 20px;
            font-weight: 700;
            line-height: 26px;
            color: #F9F9F9;
            &:hover{
                color: var(--primary_color);
            }
        }
    }
    .btn-fill{
        background-color: transparent;
        color: #FFFFFF;
        &:hover{
            background-color: var(--button_color);
            color: var(--meta_color);
        }
    }
    .close-btn{
        position: absolute;
        top: 35px;
        right: 35px;
        z-index: 22;
        cursor: pointer;
        i{
            font-size: 24px;
            color: #FFFFFF;
            &:hover{
                color: var(--primary_color);
            }
        }
    }
}
.header1.header-dark{
    position: unset;
    background-color: var(--meta_color);
}