body {
    font-size: 16px;
    font-family: var(--font_body);
    font-weight: 400;
    line-height: 25px;
    color: var(--body_color);
    background-color: #ffffff;
}

p { 
    margin: 0 0 20px 0;
    color: ar(--body_color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    font-family: var(--font_title);
    margin: 0 0 20px 0;
    color: var(--title_color);
}

h1 {
    font-size: 46px;
    line-height: 1.3;
}

h2 {
    font-size: 41px;
    line-height: 1.4;
}

h3 {
    font-size: 28px;
    line-height: 1.5;
}

h4 {
    font-size: 25px;
    line-height: 1.5;
}

h5 {
    font-size: 22px;
    line-height: 1.6;
}

h6 {
    font-size: 20px;
    line-height: 1.6;
}